.LoginPage{
  width: 100vw;
  height: 100vh;
  background-image: -webkit-linear-gradient(20deg, #667eea 0%, #764ba2 100%);
  background-image: -o-linear-gradient(20deg, #667eea 0%, #764ba2 100%);
  background-image: linear-gradient(70deg, #667eea 0%, #764ba2 100%);
  overflow:hidden;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.Login{
  text-align: center;
  background-color: transparent;
}

.LoginInput{
  width: 250px;
  height: 40px;
  -webkit-box-shadow: 5px 5px 20px 3px #666;
          box-shadow: 5px 5px 20px 3px #666;
  margin-bottom: 30px;
  font-size: 14px;
  padding-left: 30px;
  padding-right: 30px;
  border: 1px solid #eee;
  border-radius: 50px;
}

.LoginInput::-webkit-input-placeholder{
  color:#555;
}

.LoginInput:focus{
  outline: none;
  -webkit-box-shadow: 5px 5px 25px 3px #555;
          box-shadow: 5px 5px 25px 3px #555;
  -webkit-animation: pop 0.2s ease forwards;
          animation: pop 0.2s ease forwards;
}

.LoginBtn{
  border-radius: 50%;
  height: 60px;
  width: 60px;
  outline: none;
  border: none;
  background-color: #fff;
  -webkit-box-shadow: 5px 5px 20px 3px #555;
          box-shadow: 5px 5px 20px 3px #555;
  cursor: pointer;
}

@-webkit-keyframes pop{
  0%{
    -webkit-transform:scale(1);
            transform:scale(1);
  }
  25%{
    -webkit-transform:scale(1.05);
            transform:scale(1.05);
  }
  100%{
    -webkit-transform:scale(1.03);
            transform:scale(1.03);
  }
}

@keyframes pop{
  0%{
    -webkit-transform:scale(1);
            transform:scale(1);
  }
  25%{
    -webkit-transform:scale(1.05);
            transform:scale(1.05);
  }
  100%{
    -webkit-transform:scale(1.03);
            transform:scale(1.03);
  }
}



