.TextBox::-webkit-input-placeholder{
  color: #aaa;
}

img{
  height: 20px;
  width: auto;
}

.Send{
  background-image: -webkit-linear-gradient(20deg, #667eea 0%, #764ba2 100%);
  background-image: -o-linear-gradient(20deg, #667eea 0%, #764ba2 100%);
  background-image: linear-gradient(70deg, #667eea 0%, #764ba2 100%);  
  outline: none;
  border: none;
  cursor: pointer;
  width: 49px;
  height: 49px;
  border-radius: 50%;
  position: absolute;
  top:-27px;
  right: 10%;
  -webkit-box-shadow: 5px 5px 20px 3px #bbb;
          box-shadow: 5px 5px 20px 3px #bbb;
  -webkit-transform-origin: 50% 50%;
      -ms-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
}

.Send:hover{
  -webkit-animation: pop 0.2s linear forwards;
          animation: pop 0.2s linear forwards; 
}


@-webkit-keyframes pop{
  0%{
    -webkit-transform: scale(1);
            transform: scale(1)
  }
  30%{
    -webkit-transform: scale(1.3);
            transform: scale(1.3)
  }
  100%{
    -webkit-transform: scale(1.1);
            transform: scale(1.1)
  }
}


@keyframes pop{
  0%{
    -webkit-transform: scale(1);
            transform: scale(1)
  }
  30%{
    -webkit-transform: scale(1.3);
            transform: scale(1.3)
  }
  100%{
    -webkit-transform: scale(1.1);
            transform: scale(1.1)
  }
}


