.Messages{
 position: fixed;
 width: 100%;
 overflow-x: none;
 overflow-y: auto;
 height: calc(100% - 80px);
}

.empty{
  opacity: 0.5;
  font-style: italic;
  position: absolute;
  bottom: 20px;
  left: 50%;
  margin-left: -50px;
  width:100px;
  text-align: center;
}


